export default {
	data() {
		return {
			totalMoney: '0',
			dengji: 0,
			dengjides: '生成海报'
		};
	},
	onShow: function(e) {
		//this.getMoney();
		let userId = this.$queue.getData("userId");
		if (userId) {
			this.getUserInfo(userId)
		}
	},
	methods: {
		yaoqing() {
			uni.navigateTo({
				url: '/pages/member/yaoqing'
			});
		},
		//升级规则弹框
		shengJiMethod() {
			uni.showModal({
				title: '升级规则',
				content: '每成功邀请2位好友提升1级\n最高10级每级对应相应的返现比例\n\n邀请方式：\n1、点击右下方生成海报按钮\n2、将海报分享好友、群或朋友圈\n让好友扫码关注即可升级',
				showCancel: true,
				cancelText: '关闭',
				confirmText: '生成海报',
				success: res => {
					if (res.confirm) {
						uni.navigateTo({
							url: '/pages/share/shares'
						})
					}
				}
			});
		},
		share() {

			uni.navigateTo({
				url: '/pages/share/shares'
			});
		},
		getUserInfo(userId) {
			this.$Request.getT('/user/' + userId).then(res => {
				if (res.status === 0) {
					let grade = res.data.grade;
					if (grade == 0.3) {
						this.dengji = 0;
						this.dengjides = '还可升级10级';
					} else if (grade == 0.34) {
						this.dengji = 1;
						this.dengjides = '还可升级9级';
					} else if (grade == 0.38) {
						this.dengji = 2;
						this.dengjides = '还可升级8级';
					} else if (grade == 0.42) {
						this.dengji = 3;
						this.dengjides = '还可升级7级';
					} else if (grade == 0.46) {
						this.dengji = 4;
						this.dengjides = '还可升级6级';
					} else if (grade == 0.5) {
						this.dengji = 5;
						this.dengjides = '还可升级5级';
					} else if (grade == 0.54) {
						this.dengji = 6;
						this.dengjides = '还可升级4级';
					} else if (grade == 0.58) {
						this.dengji = 7;
						this.dengjides = '还可升级3级';
					} else if (grade == 0.62) {
						this.dengji = 8;
						this.dengjides = '还可升级2级';
					} else if (grade == 0.66) {
						this.dengji = 9;
						this.dengjides = '还可升级1级';
					} else if (grade == 0.7) {
						this.dengji = 10;
						this.dengjides = '已到达最高等级';
					}
				}
			});
		},
		getMoney() {
			let that = this;
			let relationId = this.$queue.getData('relation_id');
			if (relationId) {
				this.$Request.getT('/user/getCount?invitation=' + relationId).then(res => {
					if (res.status === 0 && res.data) {
						that.totalMoney = res.data;
					} else if (res.status === -102) {
						this.$queue.showToast(res.msg);
						this.$queue.logout();
						uni.navigateTo({
							url: '/pages/public/login'
						});
					} else {
						that.totalMoney = '0';
					}
				});
			}
		},
		getOut() {
			let that = this;
			let relationId = this.$queue.getData('relation_id');
			if (relationId) {
				if (this.totalMoney > 2) {
					that.$queue.showLoading('结算中...');
					this.$Request.getT('/user/getCash?invitation=' + relationId).then(res => {
						if (res.status === 0 && res.data) {
							uni.showModal({
								showCancel: false,
								title: '结算成功',
								content: '请返回【我的】点击【立即提现】'
							});
							that.getMoney();
						}
						uni.hideLoading();
					});
				} else {
					uni.showModal({
						showCancel: false,
						title: '结算提醒',
						content: '邀请必须够3人才可申请结算'
					});
				}
			} else {
				uni.navigateTo({
					url: '/pages/public/login'
				});
			}
		}
	}
};
